}
ul,
li {
  margin: 0;
  padding: 0;
}
li {
  list-style: none;
}
button,
input {
  outline: 0;
  font-size: 14px;
}
.flex {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
#app {
  width: 10rem;
  height: 100%;
  background: #fff;
  margin: 0 auto;
  position: relative;
}

.index-page {
  padding: 50px 0;
  text-align: center;
}
.begin-btn {
  width: 3.8rem;
  height: 1rem;
  background: #e14f4f;
  color: #fff;
  border-radius: 0.5rem;
  border: 0;
}

.time-score {
  width: 100%;
  height: 4.133rem;
  background: #dd4b4b;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: 0.8rem 1rem;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  position: relative;
}
.time-score .left-time {
  text-align: center;
}
.time-score .left-time .time {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.time-score .left-time .tag {
  font-size: 60px;
  line-height: 1.733rem;
  margin: 0 10px;
  color: #ffc7c7;
}
.time-score .left-time .item {
  width: 1.6rem;
  height: 1.733rem;
  display: inline-block;
  background: #fff;
  font-size: 30px;
  color: #dd4b4b;
  line-height: 1.733rem;
  border-radius: 10px;
}
.time-score .time-desc {
  color: #fff;
  font-size: 14px;
  margin-top: 0.27rem;
}
.begin-tip {
  width: 4.64rem;
  height: 1.067rem;
  background: url(data:image/png;base64,) no-repeat;
  background-size: 100%;
  position: absolute;
  bottom: -0.5335rem;
  left: 50%;
  margin-left: -2.32rem;
  color: #fff;
  text-align: center;
  line-height: 1rem;
  font-size: 18px;
}

.item-con {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  padding: 1.5rem 1rem;
}
.item-con .question-item {
  width: 100%;
  border: 1px solid #dbdbdb;
  padding: 0.35rem;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  font-size: 14px;
  text-align: center;
  position: relative;
}
.item-con .question-item:first-child {
  border-bottom: 0;
}
.item-con .num .remain {
  padding: 0.1rem 0.2rem;
  background: #dd4b4b;
  border-radius: 4px;
  color: #fff;
  margin-left: 0.1rem;
}
.item-con .triangle {
  display: block;
  width: 0;
  height: 0;
  border-width: 10px;
  border-style: solid;
  border-color: #dbdbdb transparent transparent transparent;
  position: absolute;
  bottom: -20px;
  left: 50%;
  margin-left: -10px;
}
.item-con .triangle:after {
  content: "";
  display: block;
  width: 0;
  height: 0;
  border-width: 10px;
  border-style: solid;
  border-color: #fff transparent transparent transparent;
  position: absolute;
  top: -11px;
  left: -10px;
}
.question-lists {
  margin-top: 30px;
}
.question-lists li {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  line-height: 40px;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.question-lists input[type="radio"] {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  width: 0.5rem;
  height: 0.5rem;
  outline: 0;
  margin-right: 0.25rem;
  background: url("data:image/png;base64,")
    no-repeat;
  background-size: 100% 100%;
}
.question-lists input[type="radio"]:checked {
  background: url("data:image/png;base64,")
    no-repeat;
  background-size: 100% 100%;
}
.btns {
  margin-top: 0.5rem;
  text-align: center;
}
.btns button {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  border: 0;
  border-radius: 0.5rem;
  width: 2.3rem;
  height: 1rem;
  color: #fff;
}
.pass-chance-num {
  text-align: center;
  margin-top: 0.5rem;
  color: #666;
}

#modal {
