}
#doneModal .tips {
  width: 4.67rem;
  height: 1rem;
  background: url(data:image/png;base64,) no-repeat;
  background-size: cover;
  position: absolute;
  top: -0.5rem;
  left: 50%;
  margin-left: -2.245rem;
  color: #fff;
  line-height: 1rem;
  -webkit-box-shadow: 1px 7px 7px -5px #9e7620;
          box-shadow: 1px 7px 7px -5px #9e7620;
}
#doneModal .top-score .score {
