  display: -ms-flexbox;
  display: flex;
}
#doneModal .result .item {
  text-align: center;
  color: #fff;
  margin-left: 10px;
}
#doneModal .result .item .num {
  padding: 0.3rem 0.15rem;
  width: 0.7rem;
  background: #fff;
  font-size: 22px;
  color: #dd4b4b;
