}
#doneModal .btns {
  padding: 0 1rem;
  margin-bottom: 1rem;
}
#doneModal .btns .row {
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  margin-bottom: 0.25rem;
}
#doneModal .btns .share-btn {
  width: 100%;
}

#score-wrap .banner {
